// @flow
import React from 'react';
import Typing from 'react-typing-animation';


const A = props => <a {...props} without rel="noopener noreferrer" target="_blank" className="has-text-primary">{props.children}</a>;

export default function Home() {
  const devs = ['front-end', 'react', 'react-native', 'javascript'];

  return (
    <section>

      <Typing>
        <h1 className="title is-1">Hi, <br /> <span style={{ fontSize: 60 }}>I'm Edu</span></h1>
      </Typing>

      <h3 className="subtitle">
        <Typing loop>
          {devs.map(dev => (
            <>
              <>{dev}</>
              <Typing.Delay ms={2500} />
              <Typing.Backspace count={dev.length + 1} />
            </>
          ))}
        </Typing>
        {' '}developer
      </h3>

      <br /><br />

      <p>
        passionate about javascript, i believe you can make a better world by
        engaging people and sharing experiences with communities.
        currently live in <A href="//wikipedia.org/wiki/Aracaju">aracaju, brazil</A>, <br />
        i'm a <A href="//www.meetup.com/gdgaracaju/">gdg organizer</A> and founder of <A href="//www.meetup.com/ReactAJU/">ReactAJU</A>
      </p>

    </section>
  );
}
