// @flow

import React, { useState } from 'react';
import { hot } from 'react-hot-loader/root';

import {
  Router, Location, navigate, Link,
} from '@reach/router';
import { TransitionGroup, CSSTransition } from 'react-transition-group';


// sections
import Home from './screens/home';
import Experiences from './screens/experiences';
import Skills from './screens/skills';
import Projects from './screens/projects';
import Contact from './screens/contact';

const throttle = (func, limit) => {
  let inThrottle;
  return function () {
    const args = arguments;
    const context = this;
    if (!inThrottle) {
      func.apply(context, args);
      inThrottle = true;
      setTimeout(() => inThrottle = false, limit);
    }
  };
};

function App() {
  const [currentRoute, setRoute] = useState(0);
  const routes = ['/', '/exp'];


  const onScroll = async (event) => {
    const delta = Math.sign(event.deltaY);
    if (delta > 0) {
      await setRoute((currentRoute + 1) % routes.length);
    } else {
      await setRoute((currentRoute - 1) % routes.length);
    }
    navigate(routes[currentRoute]);
  };

  // document.addEventListener('wheel', throttle(onScroll, 2000));

  const renderRoutes = () => (
    <FadeTransitionRouter>
      <Home path="/" />
      <Experiences path="/experiences" />
      <Skills path="/skills" />
      <Projects path="/projects" />
      <Contact path="/contact" />
    </FadeTransitionRouter>
  );

  return (
    <div className="App">

      <div className="hero is-black is-fullheight">
        <div className="hero-head">
          <div className="container">
            <div className="navbar">
              <div className="navbar-menu">
                <div className="navbar-end">
                  <Link to="/" className="navbar-item">
                    about me
                  </Link>

                  <Link to="/experiences" className="navbar-item">
                    experiences
                  </Link>

                  <Link to="/skills" className="navbar-item">
                    skills
                  </Link>

                  <Link to="/projects" className="navbar-item">
                    projects
                  </Link>

                  <Link to="/contact" className="navbar-item">
                    contact
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/*  */}
        <div className="hero-body">
          <div className="container">
            {renderRoutes()}
          </div>
        </div>

        {/* footer */}
        <div className="hero-foot">
          <div className="container">
            <div className="navbar">
              <div className="navbar-menu">
                <div className="navbar-start">
                  <a href="//github.com/eduardoborges" className="navbar-item">
                    github
                  </a>

                  <a href="//linkedin.com/in/euduardoborges" className="navbar-item">
                    linkedin
                  </a>

                  <a to="//telegram.me/eduardoborges" className="navbar-item">
                    telegram
                  </a>

                  <a href="https://api.whatsapp.com/send?phone=5575991265577" className="navbar-item">
                    whatsapp
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  );
}


const FadeTransitionRouter = props => (
  <Location>
    {({ location }) => (
      <TransitionGroup className="transition-group">
        <CSSTransition key={location.key} classNames="fade" timeout={500}>
          <Router location={location} className="router">
            {props.children}
          </Router>
        </CSSTransition>
      </TransitionGroup>
    )}
  </Location>
);

export default hot(App);
